  background-size: cover;
  background-repeat: no-repeat;
  margin-left: 1em; }

.header__nav, .header__nav_list {
  display: flex;
  flex-direction: row;
  font-family: "Roboto Mono", monospace;
  justify-content: space-between;
  align-items: center; }

.header__nav_list {
  font-size: 1.25em;
  margin-right: 1em; }
  .header__nav_list .header__nav_listitem {
    padding: 0 .25em .5em 0;
    margin: 0 10px;
    color: #fff; }
    .header__nav_list .header__nav_listitem:hover {
      border-bottom: 1px solid #fff; }
    .header__nav_list .header__nav_listitem a {
      color: #fff; }

.mainhero {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  @media (max-width: 950px) {
    .mainhero {
      height: 70vh; }
      .mainhero .main_primary_text {
        font-size: 3em; }
      .mainhero .main_secondary_text {
        font-size: 1.5em; } }
  @media (max-width: 550px) {
    .mainhero .main_primary_text {
      font-size: 1.5em; }
    .mainhero .main_secondary_text {
      font-size: 1em; } }

.main_primary_text, .main_secondary_text {
  display: block; }

.main_primary_text {
  font-size: 5em;
  font-weight: 900;
