.main_secondary_text {
  font-size: 2.5em;
  top: 53%;
  right: 50%;
  padding: 2em; }

.main_open-brace {
  margin-right: 15px; }

.main_close-brace {
  margin-left: 15px; }

#aboutme h2 {
  text-align: right;
  margin-right: 1em; }

.aboutme_content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: column; }

.about_picture_figure {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column; }

.rounded_marquis {
  height: 350px;
  width: 350px;
  border-radius: 40px;
  margin: auto;
  display: block; }

.aboutme__paragraph {
  padding: 2em;
