  font-weight: 500; }
  .aboutme__paragraph::first-letter {
    margin-left: 3em; }
  .aboutme__paragraph .important-bold {
    font-weight: 900;
    font-style: italic;
    font-weight: .25em; }

.bottom {
  margin-top: none;
  padding: 0 2em 2em 2em; }

#projects h2 {
  margin-left: 1em; }

.projects__card {
  position: relative;
  height: 450px;
  margin: 3em;
  overflow: hidden;
  border-radius: 5px;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  transform: translatez(0); }
  @media (max-width: 700px) {
    .projects__card {
      background-attachment: scroll;
      background-position: center; } }

.project_card_information {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  z-index: -5;
  position: absolute;
  background-color: #CF3A39;
  right: 0;
  top: 0;
  opacity: .85;
  width: 50%;
  height: 100%;
  z-index: 5; }
  @media (max-width: 700px) {
    .project_card_information {
      width: 100%; } }
  .project_card_information .project__name {
    font-size: 3em;
    text-align: center; }
    @media (max-width: 500px) {
      .project_card_information .project__name {
        font-size: 2.5em; } }
  .project_card_information .project__description {
    font-size: 1.5em;
    text-align: center;
    padding: .5em; }
    @media (max-width: 500px) {
      .project_card_information .project__description {
        font-size: 1.25em; } }
  .project_card_information .project__icons {
    display: flex;
    justify-content: space-evenly;
    align-content: center; }
    .project_card_information .project__icons .github__icon, .project_card_information .project__icons .website__icon {
      width: 100px;
      height: 100px;
      background-size: cover;
      z-index: 5;
      display: block;
      margin: auto; }
      @media (max-width: 500px) {
        .project_card_information .project__icons .github__icon, .project_card_information .project__icons .website__icon {
          height: 70px;
          width: 70px; } }
    .project_card_information .project__icons .github__icon {
      background-image: url("/img/github.svg"); }
    .project_card_information .project__icons .website__icon {
      background-image: url("/img/website.svg"); }
    .project_card_information .project__icons .project__icons-wrapper {
      padding: 0 2em 2em 2em; }
    .project_card_information .project__icons .project__icon-text {
      font-weight: bold; }
      @media (max-width: 470px) {
        .project_card_information .project__icons .project__icon-text {
          display: none; } }

.projects__header__secondary {
  font-size: 2em;
  margin-left: 1em;
  text-align: center;
  margin-bottom: 1em; }
  @media (max-width: 550px) {
    .projects__header__secondary {
      font-size: 1.25em;
      margin-left: 0 !important; } }

.tech__icon-wrapper {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: center;
  align-items: center; }

.tech__icons {
  margin: 2em  4em;
